@import "../../shared";

.indice {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;

  width: 300px;
  height: 100vh;

  background: $maincolor;
  overflow: hidden;
  overflow-y: auto;

  .indice_sections {
    .section_title {
      padding: 15px;

      text-transform: uppercase;
      color: $white;
      font-family: $opensans;
      font-size: 20px;
      font-weight: 300;
      letter-spacing: 1px;
    }

    .section_list {
      padding-bottom: 30px;

      .section_item {
        a {
          display: block;

          width: 100%;
          padding: 0 30px;

          color: $white;
          font-family: $opensans;
          font-size: 12px;
          line-height: 40px;
          text-transform: uppercase;
          letter-spacing: 1px;

          transition: 0.3s;

          &:hover {
            background: rgba($black, 0.1);
          }
        }
      }
    }
  }
}
