@import "../../shared";

.character_top {
  background: $maincolor;

  padding: 15px;

  .character_title {
    padding: 30px 0;

    text-align: center;

    .character_photoplayer,
    .character_name {
      display: inline-block;
      vertical-align: baseline;

      font-size: 20px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $white;
      font-weight: 300;
    }

    .character_name {
      font-size: 40px;

      margin-left: 10px;
    }
  }

  .top_wrapper {
    display: flex;
    align-items: center;

    .character_picture {
      width: 325px;

      border: 2px solid $white;
    }

    .character_info {
      padding: 15px 0;
      margin-left: 30px;

      flex-grow: 1;

      text-align: center;
      color: $white;
      text-transform: uppercase;

      .character_info_list {
        display: grid;
        align-items: center;

        grid-template:
          "" / calc(20% - (40px / 5)) calc(20% - (40px / 5)) calc(
            20% - (40px / 5)
          )
          calc(20% - (40px / 5)) calc(20% - (40px / 5));
        grid-gap: 10px;

        width: 100%;
        padding: 15px 0;

        &:not(:first-of-type) {
          border-top: 1px solid rgba($white, 0.3);
        }

        .character_info_list_item {
          .character_info_list_item_label {
            background: rgba($black, 0.3);
          }

          .character_info_list_item_value {
            background: rgba($black, 0.1);
          }

          .character_info_list_item_label,
          .character_info_list_item_value {
            padding: 0 5px;

            line-height: 30px;
            font-size: 14px;

            text-align: center;
          }
        }
      }
    }
  }
}
