@import "../../shared";

.itens_title {
  padding: 30px 0 15px;

  font-size: 30px;
  font-weight: 300;
  color: $maincolor;
  text-transform: uppercase;
  text-align: center;
}

.itens_list {
  display: grid;
  grid-template:
    "" / calc(25% - (45px / 4)) calc(25% - (45px / 4)) calc(25% - (45px / 4))
    calc(25% - (45px / 4));
  grid-gap: 15px;
  grid-auto-rows: 1fr;

  padding: 0 0 30px;

  .itens_item {
    .item_title {
      display: flex;

      background: $maincolor;

      > div {
        padding: 10px;

        color: $white;
        font-size: 16px;
        text-transform: uppercase;
        font-weight: 300;
        text-align: center;

        &:first-of-type {
          width: 80%;
        }

        &:last-of-type {
          width: 20%;

          border-left: 1px solid $lightishgrey;

          text-transform: none;
        }
      }
    }

    .item_description {
      display: flex;
      align-items: center;

      height: calc(100% - 40px);

      background: $white;

      div {
        &:first-of-type {
          width: 80%;
          padding: 10px;

          line-height: 140%;
          font-size: 10px;
          text-align: justify;

          * {
            line-height: 140%;
          }
        }

        &:last-of-type {
          display: flex;
          align-items: center;

          border-left: 1px solid $lightishgrey;

          width: 20%;
          height: 100%;

          img {
            display: block;
            margin: auto;

            max-width: 32px;
          }
        }
      }
    }
  }
}
