@import "../shared";

.rules {
  padding: 30px 60px 30px 360px;
  min-height: 100vh;

  background: $black;

  .content {
    padding: 15px;

    overflow: hidden;
    overflow-y: auto;

    color: $white;

    .rule_title {
      padding-bottom: 30px;

      text-align: center;
      color: $maincolor;
      text-transform: uppercase;
      font-family: $opensans;
      font-size: 40px;
      font-weight: 300;
    }

    .rule_sections {
      .section_item {
        &:not(:first-of-type) {
          margin-top: 30px;
          padding-top: 30px;
          border-top: 1px solid rgba($white, 0.5);
        }

        .section_title {
          padding-bottom: 15px;

          color: $maincolor;
          text-transform: uppercase;
          font-family: $opensans;
          font-weight: 300;
          font-size: 24px;
        }

        .section_text {
          font-family: $opensans;
          font-size: 14px;
          line-height: 140%;
          text-align: justify;

          .color {
            color: $maincolor;
          }

          .underline {
            text-decoration: underline;
          }

          .small {
            font-size: 11px;
          }

          a {
            text-decoration: underline;
            color: $maincolor;
            transition: color 0.3s;

            &:hover {
              color: $grey;
            }
          }

          * {
            line-height: 140%;
          }

          .table {
            th {
              text-transform: uppercase;
            }

            th,
            td {
              vertical-align: middle;

              background: $maincolor;

              padding: 10px 15px;

              text-align: center;
            }

            &.table-atributos {
              width: 100%;

              th:last-of-type {
                text-align: left;
              }

              td:last-of-type {
                text-transform: none;
                text-align: left;
              }
            }

            &.table-subproducts {
              width: 100%;

              td:nth-child(2n) {
                text-align: left;
              }

              td:nth-child(2n + 1) {
                width: 100px;
              }
            }
          }
        }
      }
    }
  }
}
