@import "../../shared";

.vantagenspericiasmissions {
  display: grid;
  grid-template:
    "" /
    calc((100% / 3) - (30px / 3))
    calc((100% / 3) - (30px / 3))
    calc((100% / 3) - (30px / 3));
  grid-gap: 15px;

  padding: 0 0 30px;

  .vantagenspericiasmissions_list {
    .vantagenspericiasmissions_title {
      padding: 30px 0 15px;

      font-size: 30px;
      font-weight: 300;
      color: $maincolor;
      text-transform: uppercase;
      text-align: center;
    }

    .vantagenspericiasmissions_item {
      background: $white;

      &:not(:last-of-type) .vantagenspericiasmissions_wrapper {
        border-bottom: 1px solid $lightishgrey;
      }

      .vantagenspericiasmissions_wrapper {
        padding: 15px 5px;
        margin: 0 10px;

        .mission {
          text-transform: uppercase;
        }

        > :not(.mission):first-of-type {
          font-size: 16px;
          font-weight: 700;
          text-transform: uppercase;
        }

        > :not(.mission):last-of-type {
          padding: 15px 0 0;

          line-height: 140%;
          text-align: justify;
        }
      }
    }
  }
}
