@import "../../shared";

.battleachievements {
  display: grid;
  grid-template:
    "" /
    calc(50% - 7.5px)
    calc(50% - 7.5px);
  grid-gap: 15px;

  padding: 0 0 30px;

  .battleachievements_title {
    padding: 30px 0 15px;

    font-size: 30px;
    font-weight: 300;
    color: $maincolor;
    text-transform: uppercase;
    text-align: center;
  }

  .battleachievements_list {
    display: grid;
    grid-template:
      "" /
      calc(25% - (3px / 4))
      calc(25% - (3px / 4))
      calc(25% - (3px / 4))
      calc(25% - (3px / 4));
    grid-gap: 1px;

    .battleachievements_item {
      background: $white;
      padding: 15px 5px;

      text-align: center;

      > :first-of-type {
        display: flex;
        align-content: center;
        align-items: center;

        height: 35px;

        margin: auto;

        > div {
          margin: auto;

          img {
            max-height: 35px;
          }
        }
      }

      > :last-of-type {
        font-size: 12px;
        font-weight: 300;

        height: 22px;
        padding-top: 10px;
      }
    }
  }
}
