@import "../../shared";

.reputacoes {
  .reputacoes_title {
    padding: 30px 0 15px;

    font-size: 30px;
    font-weight: 300;
    color: $maincolor;
    text-transform: uppercase;
    text-align: center;
  }

  .reputacoes_list {
    display: grid;
    grid-template:
      "" /
      calc(100% / 4 - (15px / 4))
      calc(100% / 4 - (15px / 4))
      calc(100% / 4 - (15px / 4))
      calc(100% / 4 - (15px / 4));
    grid-gap: 5px;

    .reputacao_item {
      display: flex;

      font-size: 16px;

      > div {
        padding: 10px;
        line-height: 30px;

        &:first-of-type {
          width: calc(80% - 1px);
          margin-right: 1px;

          background: $white;
        }
        &:last-of-type {
          width: 20%;

          background: $lightgrey;

          text-align: center;
        }
      }
    }
  }
}
