@import "../../shared";

.fichario {
  .fichario_title {
    padding-bottom: 30px;

    text-align: center;
    color: $maincolor;
    text-transform: uppercase;
    font-family: $opensans;
    font-size: 40px;
    font-weight: 300;
  }

  .fichario_list {
    display: grid;
    grid-template-columns: calc(50% - (5px / 2)) calc(50% - (5px / 2));
    grid-gap: 5px;

    .guild_section {
      display: flex;
      align-items: center;

      background: $darkgrey;
      padding: 30px;

      .guild_picture {
        width: 200px;
        border: 1px solid $white;
      }

      .guild_info {
        width: calc(100% - 200px);
        padding-left: 30px;

        font-weight: 300;

        div {
          font-size: 16px;
          line-height: 160%;
        }

        .title {
          font-weight: 700;
          font-size: 40px;
        }
      }
    }
  }
}
