@import "../../shared";

.fichario {
  .fichario_title {
    padding-bottom: 30px;

    text-align: center;
    color: $maincolor;
    text-transform: uppercase;
    font-family: $opensans;
    font-size: 40px;
    font-weight: 300;
  }

  .fichario_list {
    display: grid;
    grid-template-columns: calc(50% - (5px / 2)) calc(50% - (5px / 2));
    grid-gap: 5px;

    .player_section {
      display: flex;

      .player_code {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 150px;

        background: $maincolor;

        font-size: 14px;
      }

      .player_characters {
        width: calc(100% - 155px);
        margin-left: 5px;
        padding: 15px;

        background: $darkgrey;

        .character {
          display: inline-block;

          padding: 15px;
          width: 175px;

          text-align: center;
          color: $white;

          transition: background-color 0.3s;

          &:hover {
            background: rgba($white, 0.1);
          }

          .character_picture {
            display: inline-block;

            width: 100px;
            height: 100px;

            border: 1px solid $white;

            overflow: hidden;
          }

          .character_name {
            margin-top: 15px;

            span {
              text-transform: uppercase;
            }

            div {
              margin-top: 5px;
            }
          }
        }
      }
    }
  }
}
