@import "../../shared";

.historiapersonalidade {
  display: grid;
  grid-template: "" / calc(50% - 7.5px) calc(50% - 7.5px);
  grid-gap: 15px;

  padding: 0 0 30px;

  .historiapersonalidade_list {
    .historiapersonalidade_title {
      padding: 30px 0 15px;

      font-size: 30px;
      font-weight: 300;
      color: $maincolor;
      text-transform: uppercase;
      text-align: center;
    }

    .historiapersonalidade_item {
      background: $white;

      .historiapersonalidade_wrapper {
        padding: 15px 5px;
        margin: 0 10px;

        line-height: 140%;
        text-align: justify;
      }
    }
  }
}
