@import "../../shared";

.pokemons_title {
  padding: 30px 0 15px;

  font-size: 30px;
  font-weight: 300;
  color: $maincolor;
  text-transform: uppercase;
  text-align: center;
}

.pokemons_list {
  display: grid;
  grid-template: "" / calc(100% / 3 - (30px / 3)) calc(100% / 3 - (30px / 3)) calc(
      100% / 3 - (30px / 3)
    );
  grid-gap: 15px;
  grid-auto-rows: 1fr;

  padding: 0 0 30px;

  .pokemon_item {
    .pokemon_title {
      display: flex;

      background: $maincolor;

      > div {
        display: flex;
        align-items: center;

        width: 50%;
        padding: 10px;

        &:first-of-type {
          border-right: 1px solid $lightishgrey;
        }

        &:last-of-type {
          > div:last-of-type {
            font-size: 20px;
          }
        }

        > div {
          color: $white;
          font-size: 16px;
          text-transform: uppercase;
          font-weight: 300;

          &:first-of-type {
            width: 60%;
          }

          &:last-of-type {
            width: 40%;

            text-align: right;

            span {
              color: #ffb01e;
            }
          }
        }
      }
    }

    .pokemon_body {
      display: flex;
      height: calc(100% - 40px);

      background: $white;

      .pokemon_picture {
        display: flex;
        align-items: center;

        width: 50%;
        height: 100%;
        padding: 15px;

        border-right: 1px solid $lightishgrey;
      }

      .pokemon_info {
        width: 50%;
        padding: 15px;

        text-align: center;

        > div,
        > div b {
          line-height: 160%;
        }

        .pokemon_types {
          padding-bottom: 5px;

          img {
            margin: 0 3px;
          }
        }

        .pokemon_moveset {
          margin-top: 10px;

          text-align: left;

          > div {
            line-height: 140%;
          }
        }
      }
    }
  }
}
